		deletes: function (index) {
		  var that = this;
		  // 获取商品列表数据
		  let list = this.list;
		  console.log(list.length)
		  let selectAllStatus = this.selectAllStatus;
		  uni.showModal({
		    title: '提示',
		    content: '确认删除吗',
		    success: function (res) {
		      if (res.confirm) {
		        // 删除索引从1
		        list.splice(index, 1 );
		        // 页面渲染数据
		          that.list = list,
		          that.selectAllStatus = false
				  this.piece = list.length
		        // 如果数据为空
		        if (!list.length) {
		          that.hasList = false
		        } else {
					
		        }
		      } else {
		        console.log(res);
		      }
