		  let list = this.list;
		  var sk = list.length
		  let selectAllStatus = this.selectAllStatus;
		  uni.showModal({
		    title: '提示',
		    content: '确认清空购物车吗',
		    success: function (res) {
		      if (res.confirm) {
		        // 删除索引从1
		        list.splice(index, sk);
		        // 页面渲染数据
		          that.list = list,
		          that.selectAllStatus = false
		        // 如果数据为空
		        if(!list.length) {
		          that.hasList = false
		        } else {
					
		        }
		      } else {
		        console.log(res);
		      }
		    },
		    fail: function (res) {
		      console.log(res);
		    }
		  })
		}
	}
